import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "../views/HomeView.vue";
// import IndexView from '../views/IndexView.vue';
// import ShoppingCarView from '../views/ShoppingCarView';
// import PersonView from '../views/PersonView.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    /**
     * 首页
     */
    path: "/index",
    name: "index",
    component: () => import("../views/IndexView.vue"),
    // component: IndexView
  },
  {
    /**
     * 商品详情页
     */
    path: "/iteminfo",
    name: "ItemInfo",
    component: () => import("../views/ItemInfoView.vue"),
  },
  {
    /**
     * 商城页
     */
    path: "/shopping",
    name: "shopping",
    component: () => import("../views/ShoppingView.vue"),
  },
  {
    /**
     * 分类商城页
     */
    path: "/shopping/classify",
    name: "shoppingClassify",
    component: () => import("../views/ClassifyItemView.vue"),
  },
  {
    /**
     * 购物车
     */
    path: "/shoppingcar",
    name: "shoppingcar",
    component: () => import("../views/ShoppingCarView.vue"),
    // component: ShoppingCarView
  },
  {
    /**
     * 个人中心页
     */
    path: "/person",
    name: "person",
    component: () => import("../views/PersonView.vue"),
    // component: PersonView
  },
  {
    /**
     * 收货地址管理
     */
    path: "/address",
    name: "address",
    component: () => import("../views/AddressView.vue"),
  },
  {
    /**
     * 商品管理
     */
    path: "/manage",
    name: "manage",
    component: () => import("../views/ManageView.vue"),
  },
  {
    /**
     * 订单管理
     */
    path: "/ordermanage",
    name: "ordermanage",
    component: () => import("../views/OrderManageView.vue"),
  },
  {
    /**
     * 商品分类管理
     */
    path: "/admin/itemclass",
    name: "itemclass",
    component: () => import("../views/admin/ItemClassifyManageView.vue"),
  },
  {
    /**
     * 我的订单
     */
    path: "/orders",
    name: "orders",
    component: () => import("../views/OrdersView.vue"),
  },
  {
    /**
     * 活动页
     */
    path: "/news/activity",
    name: "activity",
    component: () => import('../views/news/ActivityView.vue'),
  },
  {
    /**
     * 活动管理页
     */
    path: "/admin/activity",
    name: "adminactivity",
    component: () => import('../views/admin/ActivityManageView.vue'),
  },
  {
    path: "*",
    name: "notFound",
    component: () => import('../views/error/NotFoundView.vue')
  }
];

const router = new VueRouter({
  routes,
});

// router.afterEach((to, from, next) => {
//   // 路由导航后执行页面刷新
//   next();
// });

export default router;
